.milk {
  margin-bottom: 35px;

  @media (min-width: $tablet-width) {
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 31px;
  }
}

.milk__list {
  list-style-type: none;
}

.milk__label-radio {
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  display: grid;
  grid-template-columns: 30px 1fr;
  cursor: pointer;

  &:hover .milk__control::before,
  &:focus .milk__control::before {
    border: 2px solid $primary-lighter;
  }

  &:hover .milk__control::after,
  &:focus .milk__control::after {
    content: "";
    background-clip: padding-box;
    background-color: $primary-opacity;
    border: 4px solid transparent;
    border-radius: 50%;
    height: 24px;
    grid-column: 1/2;
    grid-row: 1/2;
  }

  &--disabled .milk__control {
    color: $base-gray;
  }

  &--disabled .milk__control::before {
    border: 2px solid $light-grey;
  }

  &--disabled .milk__input:checked + .milk__control::after {
    background-color: $light-grey;
  }
}

.milk__list-item {
  margin-bottom: 20px;
  padding-left: 3px;

  @media (min-width: $tablet-width) {
    padding: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.milk__input {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 24px;
  grid-column: 1/2;
  grid-row: 1/2;
  opacity: 0;

  &:checked + .milk__control::after {
    content: "";
    background-clip: padding-box;
    background-color: $base-primary;
    border: 4px solid transparent;
    border-radius: 50%;
    height: 24px;
    grid-column: 1/2;
    grid-row: 1/2;
  }

  &:checked + .milk__control::before {
    border: 2px solid $base-primary;
  }
}

.milk__control {
  grid-column: 1/-1;
  grid-row: 1/2;
  display: grid;
  grid-template-columns: 24px 1fr;
  gap: 10px;
  align-items: center;

  &::before {
    content: "";
    border: 2px solid $base-gray;
    border-radius: 50%;
    height: 24px;
    grid-column: 1/2;
    grid-row: 1/2;
  }
}

.milk__legend {
  margin-bottom: 19px;
}
