.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.noUi-handle::before,
.noUi-handle::after {
  content: none;
}

.noUi-handle {
  width: 24px;
  height: 24px;
  background-color: rgb(255, 255, 255);
  border-radius: 50%;
  border-width: 2px;
  border-color: rgb(144, 112, 236);
  margin-top: -5px;

  &:hover,
  &:focus {
    background-color: $primary-opacity;
  }

  &:active {
    background-color: $base-primary;
  }
}

.noUi-target {
  border: none;
}

.swiper-pagination-bullet {
  width: 62px;
  height: 4px;
  margin: 0 8px;
  border-radius: 0;
  background-color: $base-white;
  opacity: 1;

  &:hover {
    background-color: $primary-opacity;
  }
}

.swiper-pagination-bullet-active {
  background-color: $base-primary;
}

