.catalog {
  font-family: "Montserrat", "Arial", sans-serif;
  color: $base-black;
  background-color: $lightest-grey;
}

.catalog__wrapper {
  width: 320px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  padding: 49px 20px 44px;

  @media (min-width: $tablet-width) {
    width: 768px;
    padding: 80px 20px 90px;
  }

  @media (min-width: $desktop-width) {
    width: 1440px;
    padding: 100px 40px 120px;
    display: grid;
    grid-template-columns: 318px 1fr;
    column-gap: 30px;
  }
}

.catalog__title {
  text-align: center;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 36px;
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    font-size: 42px;
    font-style: normal;
    font-weight: 700;
    line-height: 64px;
    margin-bottom: 20px;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1/-1;
    margin-bottom: 36px;
  }
}

.catalog__badge {
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.48px;
  text-transform: uppercase;
  color: $base-primary;
  text-align: center;
  order: -1;
  margin-bottom: 5px;

  @media (min-width: $tablet-width) {
    margin-bottom: 5px;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1/-1;
  }
}
