.card {
  display: grid;
  grid-template-columns: 1fr 1fr;
  border-radius: 4px;

  @media (min-width: $tablet-width) {
    grid-template-columns: 1fr 141px;
  }

  @media (min-width: $desktop-width) {
    &:focus,
    &:hover {
      box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.15);
    }
  }
}

.card__title {
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
  grid-column: 1/-1;
  margin-bottom: 4px;

  @media (min-width: $tablet-width) {
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 36px;
    margin-bottom: 9px;
  }
}

.card__description {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  grid-column: 1/-1;
  margin-bottom: 16px;
  padding-right: 10px;

  @media (min-width: $tablet-width) {
    margin-bottom: 20px;
  }
}

.card__price {
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 32px;
  color: $base-black;
  grid-column: 1/2;
  align-self: center;
  margin-left: -2px;
}

.card__button {
  background-color: $base-primary;
  font-family: "Montserrat", "Arial", sans-serif;
  border: none;
  color: $base-white;
  border-radius: 4px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  grid-column: 2/3;
  text-align: end;
  background-image: url("../image/icons/cartCatalogIcon.svg");
  background-repeat: no-repeat;
  background-position: center left 20px;
  min-width: 141px;
  min-height: 36px;
  padding: 5px 20px 5px 45px;
  word-break: break-all;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: $primary-lighter;
  }

  &:active {
    background-color: $primary-darker;
  }

  &:disabled {
    background-color: $base-gray;
  }

  @media (min-width: $tablet-width) {
    margin-left: 1px;
  }
}

.card__image-wrapper {
  grid-column: 1/-1;
  order: -1;
  justify-self: center;
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    margin-bottom: 27px;
  }
}
