.advantages {
  color: $base-black;
  font-family: "Montserrat", "Arial", sans-serif;
  text-align: center;
}

.advantages__wrapper {
  width: 320px;
  padding: 50px 20px 60px 20px;
  display: flex;
  flex-direction: column;
  margin: auto;

  @media (min-width: $tablet-width) {
    margin: 0 auto;
    width: 728px;
    padding: 0 0 100px 0;
  }

  @media (min-width: $desktop-width) {
    width: 1440px;
    padding: 102px 40px 118px 40px;
  }
}

.advantages__list {
  list-style-type: none;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 50px 9px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(4, 1fr);
    gap: 50px 28px;
  }
}

.advantages__title {
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 36px;
  margin-bottom: 36px;

  @media (min-width: $tablet-width) {
    font-size: 42px;
    font-style: normal;
    font-weight: 700;
    line-height: 64px;
    margin-bottom: 60px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 81px;
  }
}

.advantages__badge {
  color: $base-primary;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.48px;
  text-transform: uppercase;
  order: -1;
  margin-bottom: 5px;

  @media (min-width: $desktop-width) {
    margin-bottom: 2px;
  }
}

.advantages__heading {
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
  margin-bottom: 4px;

  @media (min-width: $tablet-width) {
    margin-bottom: 10px;
    font-size: 24px;
    line-height: 36px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 10px;
  }
}

.advantages__description {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 24px;
  }
}

.advantages__item {
  padding: 66px 0 32px 0;
  position: relative;
  background-repeat: no-repeat;
  background-position: center 0;

  @media (min-width: $tablet-width) {
    text-align: left;
    padding: 0 0 0 82px;
    background-position: left 0;
  }

  @media (min-width: $desktop-width) {
    text-align: center;
    padding: 76px 0 0 0;
    background-position: center 0;
    width: 318px;
  }

  &:last-child {
    padding-bottom: 0;
  }

  &--speed {
    background-image: url("../image/icons/speedIconAdvantages.svg");

    @media (min-width: $tablet-width) {
      background-position: 10px 3px;
    }

    @media (min-width: $desktop-width) {
      background-position: center -1px;
    }
  }

  &--fit {
    background-image: url("../image/icons/fitIconAdvantages.svg");

    @media (min-width: $tablet-width) {
      background-position: 9px 0;
    }

    @media (min-width: $desktop-width) {
      background-position: center -1px;
    }
  }

  &--energy {
    background-image: url("../image/icons/energyIconAdvantages.svg");

    @media (min-width: $tablet-width) {
      background-position: 10px -1px;
    }

    @media (min-width: $desktop-width) {
      background-position: center -1px;
    }
  }

  &--eco {
    background-image: url("../image/icons/ecoIconAdvantages.svg");

    @media (min-width: $tablet-width) {
      background-position: 10px 4px;
    }

    @media (min-width: $desktop-width) {
      background-position: center -1px;
    }
  }
}

