@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 800;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-ExtraBold.woff2") format("woff2");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.noUi-handle::before,
.noUi-handle::after {
  content: none;
}

.noUi-handle {
  width: 24px;
  height: 24px;
  background-color: rgb(255, 255, 255);
  border-radius: 50%;
  border-width: 2px;
  border-color: rgb(144, 112, 236);
  margin-top: -5px;
}
.noUi-handle:hover, .noUi-handle:focus {
  background-color: rgba(120, 89, 207, 0.25);
}
.noUi-handle:active {
  background-color: #7859cf;
}

.noUi-target {
  border: none;
}

.swiper-pagination-bullet {
  width: 62px;
  height: 4px;
  margin: 0 8px;
  border-radius: 0;
  background-color: #ffffff;
  opacity: 1;
}
.swiper-pagination-bullet:hover {
  background-color: rgba(120, 89, 207, 0.25);
}

.swiper-pagination-bullet-active {
  background-color: #7859cf;
}

@media (min-width: 768px) {
  .header__wrapper {
    width: 768px;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
  .header__wrapper {
    width: 1440px;
    margin: 0 auto;
  }
}

.navigation__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
}
@media (min-width: 768px) {
  .navigation__wrapper {
    display: flex;
    align-items: center;
    padding-right: 20px;
  }
}
@media (min-width: 1440px) {
  .navigation__wrapper {
    padding: 0 40px;
  }
}
.navigation__wrapper--nojs .navigation__additional-nav {
  display: block;
}
.navigation__wrapper--nojs .navigation__list {
  position: static;
}
.navigation__wrapper--nojs .navigation__user-item--nojs {
  display: none;
}

.navigation__logo {
  padding: 4px 0 0 20px;
}
@media (min-width: 1440px) {
  .navigation__logo {
    padding: 0;
    width: 316px;
  }
}

.navigation__logo-description {
  display: none;
}
@media (min-width: 1440px) {
  .navigation__logo-description {
    display: block;
    color: #333333;
    font-size: 12px;
    font-family: "Montserrat", "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    margin-left: 20px;
  }
}

.navigation__user-list {
  display: flex;
  list-style-type: none;
  justify-content: flex-end;
}
@media (min-width: 768px) {
  .navigation__user-list {
    order: 1;
    margin-left: auto;
  }
}

.navigation__button-login span,
.navigation__button-cart span {
  display: none;
}
@media (min-width: 1440px) {
  .navigation__button-login span,
  .navigation__button-cart span {
    display: block;
    color: #333333;
    font-size: 12px;
    font-family: "Montserrat", "Arial", sans-serif;
    font-style: normal;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: 0.24px;
    text-transform: uppercase;
    margin-left: 8px;
  }
}

.navigation__button-login {
  border: none;
  background-color: transparent;
  padding: 21px 22px 20px 22px;
  cursor: pointer;
}
.navigation__button-login:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .navigation__button-login {
    padding: 31px 22px 30px 22px;
  }
}
@media (min-width: 1440px) {
  .navigation__button-login {
    display: flex;
    align-items: center;
    margin-right: 12px;
  }
  .navigation__button-login:hover span {
    color: #7859cf;
  }
  .navigation__button-login:active {
    background-color: #f0ebff;
  }
  .navigation__button-login:hover .navigation__login-icon, .navigation__button-login:hover .navigation__cart-icon {
    fill: #7859cf;
  }
}

.navigation__button-cart {
  border: none;
  background-color: transparent;
  padding: 21px 22px 20px 22px;
  cursor: pointer;
}
.navigation__button-cart:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .navigation__button-cart {
    padding: 31px 22px 30px 22px;
  }
}
@media (min-width: 1440px) {
  .navigation__button-cart {
    display: flex;
    align-items: center;
  }
  .navigation__button-cart:hover span {
    color: #7859cf;
  }
  .navigation__button-cart:active {
    background-color: #f0ebff;
  }
  .navigation__button-cart:hover .navigation__login-icon, .navigation__button-cart:hover .navigation__cart-icon {
    fill: #7859cf;
  }
}

.navigation__button {
  position: relative;
  border: none;
  background-color: transparent;
  padding: 30px;
  cursor: pointer;
}
.navigation__button:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .navigation__button {
    display: none;
  }
}
.navigation__button--close::before {
  content: "";
  position: absolute;
  top: 40%;
  left: 50%;
  width: 16px;
  height: 2px;
  background-color: #333333;
  box-shadow: 0 6px 0 0 #333333, 0 11px 0 0 #333333;
  transform: translate(-50%, -40%);
}
.navigation__button--open::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 38%;
  width: 16px;
  height: 1px;
  background-color: #333333;
  transform: rotate(45deg) translate(0%, -50%);
}
.navigation__button--open::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 40%;
  width: 16px;
  height: 1px;
  background-color: #333333;
  transform: rotate(-45deg) translate(0%, -50%);
}

.navigation__logo-wrapper {
  display: flex;
}

.navigation__login-icon {
  fill: #333333;
}

.navigation__cart-icon {
  fill: #333333;
}

.additional-navigation {
  grid-column: 1/-1;
}
@media (min-width: 768px) {
  .additional-navigation {
    grid-column: 2/3;
    grid-row: 1;
  }
}
.additional-navigation--close {
  display: none;
}
@media (min-width: 768px) {
  .additional-navigation--close {
    display: block;
    margin-left: 67px;
  }
}
@media (min-width: 1440px) {
  .additional-navigation--close {
    margin-left: 162px;
  }
}
.additional-navigation--open {
  display: block;
}

.additional-navigation__list {
  list-style-type: none;
  text-align: center;
  width: 100%;
  position: absolute;
  z-index: 1;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .additional-navigation__list {
    display: flex;
    align-items: center;
    position: static;
  }
}
@media (min-width: 1440px) {
  .additional-navigation__list {
    width: 459px;
    justify-content: space-between;
  }
}

.additional-navigation__link {
  color: #333333;
  text-align: center;
  font-size: 12px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: 0.24px;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;
}
.additional-navigation__link:hover, .additional-navigation__link:focus {
  color: #7859cf;
}
.additional-navigation__link:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .additional-navigation__link {
    border: none;
    padding: 33px 20px 32px 20px;
  }
}
@media (min-width: 768px) {
  .additional-navigation__link--current {
    color: #7859cf;
  }
}
@media (min-width: 768px) {
  .additional-navigation__link--current::before {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 2px;
    background-color: #7859cf;
    content: "";
  }
}

.additional-navigation__item {
  padding: 20px 0 21px 0;
  border-bottom: 1px solid #f0ebff;
}
.additional-navigation__item:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .additional-navigation__item {
    border: none;
    padding: 0;
  }
}
.additional-navigation__item:first-child {
  border-top: 1px solid #f0ebff;
}
@media (min-width: 768px) {
  .additional-navigation__item:first-child {
    border: none;
  }
}

.slider {
  background-color: #f3ebe1;
}
@media (min-width: 768px) {
  .slider {
    background-color: #ffffff;
  }
}
@media (min-width: 1440px) {
  .slider {
    background-color: #f3ebe1;
  }
}
@media (min-width: 768px) {
  .slider--background::before {
    content: "";
    background-color: #f3ebe1;
    position: absolute;
    width: 100vw;
    height: 640px;
  }
}
@media (min-width: 1440px) {
  .slider--background::before {
    content: none;
  }
}
.slider--background.slider--additional-color::before {
  background-color: var(--color);
}

.slider__button-prev,
.slider__button-next {
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 11px 20px;
  border: none;
  height: 656px;
  width: 20px;
}
@media (min-width: 768px) {
  .slider__button-prev,
  .slider__button-next {
    height: 640px;
    width: 80px;
  }
}
@media (min-width: 1440px) {
  .slider__button-prev,
  .slider__button-next {
    height: 717px;
    width: 80px;
  }
}

.slider__button-prev {
  background-image: url("../image/icons/sliderCorner.svg");
  background-repeat: no-repeat;
  background-position: center;
  transform: rotate(180deg);
  margin-left: -10px;
}
.slider__button-prev:hover, .slider__button-prev:focus {
  background: linear-gradient(90deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%), url("../image/icons/sliderCorner.svg") no-repeat center;
}
.slider__button-prev:active {
  background: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%), url("../image/icons/sliderCorner.svg") no-repeat center;
}
.slider__button-prev:disabled {
  background-image: url("../image/icons/sliderCornerDisabled.svg");
}
.slider__button-prev::after {
  display: none;
}

.slider__button-next {
  background-image: url("../image/icons/sliderCorner.svg");
  background-repeat: no-repeat;
  background-position: center;
  margin-right: -10px;
}
.slider__button-next:hover, .slider__button-next:focus {
  background: linear-gradient(90deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%), url("../image/icons/sliderCorner.svg") no-repeat center;
}
.slider__button-next:active {
  background: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%), url("../image/icons/sliderCorner.svg") no-repeat center;
}
.slider__button-next::after {
  display: none;
}
.slider__button-next:disabled {
  background-image: url("../image/icons/sliderCornerDisabled.svg");
}

.slider__panel-buttons {
  position: absolute;
  top: 22px;
  display: flex;
  justify-content: space-between;
