@media (min-width: 768px) {
  .footer__shop::before {
    content: none;
  }
}

.footer__media {
  position: relative;
  margin-bottom: 49px;
}
@media (min-width: 768px) {
  .footer__media {
    margin-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .footer__media {
    padding-top: 8px;
    padding-left: 18px;
  }
}
.footer__media::before {
  content: "";
  position: absolute;
  top: -20px;
  left: calc(50% - 50vw);
  width: 100vw;
  height: 1px;
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .footer__media::before {
    content: none;
  }
}

.footer__contacts {
  position: relative;
  margin-bottom: 41px;
}
@media (min-width: 768px) {
  .footer__contacts {
    margin-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .footer__contacts {
    padding-top: 10px;
    padding-left: 10px;
  }
}
.footer__contacts::before {
  content: "";
  position: absolute;
  top: -20px;
  left: calc(50% - 50vw);
  width: 100vw;
  height: 1px;
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .footer__contacts::before {
    content: none;
  }
}

.footer__text {
  font-style: normal;
  margin-bottom: 16px;
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 6px;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  padding-top: 2px;
}
.footer__text::before {
  content: "";
  background-image: url("../image/icons/iconAddress.svg");
  width: 13px;
  height: 16px;
}

.footer__link-phone {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  text-decoration: none;
  color: #333333;
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 6px;
}
.footer__link-phone::before {
  content: "";
  background-image: url("../image/icons/iconPhone.svg");
  width: 13px;
  height: 16px;
}

.footer__copyright {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.footer__copyright:hover .footer__copyright-icon {
  fill: #7859cf;
}
@media (min-width: 768px) {
  .footer__copyright {
    grid-column: 1/-1;
    margin: auto;
  }
}
.footer__copyright::before {
  content: "";
  position: absolute;
  top: -20px;
  left: calc(50% - 50vw);
  width: 100vw;
  height: 1px;
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .footer__copyright::before {
    content: none;
  }
}

.footer__copyright-link {
  color: #bdbdbd;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  margin-right: 4px;
  text-decoration: none;
  font-family: "Montserrat", "Arial", sans-serif;
  margin-bottom: 20px;
}
.footer__copyright-link:hover span {
  color: #7859cf;
  fill: #7859cf;
}

@media (min-width: 768px) {
  .footer__content-wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    margin-bottom: 38px;
  }
}
@media (min-width: 1440px) {
  .footer__content-wrapper {
    margin-bottom: 28px;
  }
}

.footer__social-icon {
  fill: #bdbdbd;
  cursor: pointer;
}

.footer__copyright-icon {
  fill: #bdbdbd;
}