.filter {
  color: $base-black;
}

.filter__field {
  border: none;
}

.filter__form {
  margin-bottom: 35px;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 40px 30px;
    margin-bottom: 40px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
    display: flex;
    flex-direction: column;
    gap: 0;
    padding-top: 4px;
  }
}

.filter__legend {
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  color: $base-black;
  margin-bottom: 30px;
  padding-left: 3px;

  @media (min-width: $tablet-width) {
    padding: 0;
  }
}

.filter__wrapper-submit {
  display: flex;
  flex-direction: column;
  margin: 0 -3px 0 3px;

  @media (min-width: $tablet-width) {
    grid-column: 1/-1;
    display: block;
    margin: auto;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
  }
}

.filter__button-submit {
  margin-bottom: 20px;
  color: $base-white;
  text-align: center;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  border: none;
  background-color: $base-primary;
  padding: 8px 0 8px;
  border-radius: 4px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
    margin-right: 16px;
    padding: 8px 21px 8px 20px;
  }

  @media (min-width: $desktop-width) {
    margin-right: 0;
  }

  &:hover,
  &:focus {
    background-color: $primary-lighter;
  }

  &:active {
    background-color: $primary-darker;
  }

  &:disabled {
    background-color: $base-gray;
  }
}

.filter__button-reset {
  color: $base-primary;
  text-align: center;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  border: none;
  background-color: $base-white;
  padding: 8px 0 8px;
  border-radius: 4px;

  &:hover,
  &:focus {
    background-color: $primary-lighter;
    color: $base-white;
  }

  &:active {
    background-color: $primary-darker;
    color: $base-white;
  }

  &:disabled {
    background-color: $base-gray;
    color: $base-white;
  }

  @media (min-width: $tablet-width) {
    padding: 8px 20px 8px 20px;
  }
}
