.catalog-card {
  font-family: "Montserrat", "Arial", sans-serif;
  color: $base-black;
}

.catalog-card__heading-select {
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;

  @media (min-width: $tablet-width) {
    margin-right: 22px;
  }
}

.catalog-card__select {
  appearance: none;
  color: $base-primary;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  background-color: transparent;
  border: none;
  background-image: url("../image/icons/selectCorner.svg");
  background-repeat: no-repeat;
  background-position: center right 10px;
  padding: 5px 0 5px;
  font-family: "Montserrat", "Arial", sans-serif;

  &:hover,
  &:focus {
    background-color: $base-secondary;
    color: $base-primary;
  }

  &:active {
    background-image: url("../image/icons/selectCornerReverse.svg");
  }
}

.catalog-card__label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 3px;

  @media (min-width: $tablet-width) {
    justify-content: right;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 24px;
  }
}

.catalog-card__list {
  list-style-type: none;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    margin-bottom: 70px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 60px;
  }
}

.catalog-card__item-card {
  background-color: $base-white;
  padding: 40px 20px 20px;

  @media (min-width: $tablet-width) {
    padding: 50px 31px 30px 30px;
  }
}

.catalog-card__item {
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }

  &:last-child {
    margin-bottom: 40px;

    @media (min-width: $tablet-width) {
      margin-bottom: 0;
    }
  }
}

.catalog-card__pagination-list {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  justify-content: center;

  @media (min-width: $tablet-width) {
    justify-content: right;
  }
}

.catalog-card__pagination-link {
  min-width: 40px;
  min-height: 40px;
  text-align: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  color: $base-primary;
  background-color: $base-white;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  border-radius: 4px;

  &:hover,
  &:focus {
    background-color: $primary-lighter;
    color: $base-white;
  }

  &:active {
    background-color: $primary-darker;
  }

  &:disabled {
    background-color: $light-grey;
  }

  &--prev {
    background-image: url("../image/icons/ArrowPagination.svg");
    background-position: center;
    transform: rotate(180deg);

    @media (min-width: $tablet-width) {
      transform: rotate(0deg);
      background-image: none;
      padding: 8px 20px;
    }
  }

  &--next {
    background-image: url("../image/icons/ArrowPagination.svg");
    background-position: center;

    @media (min-width: $tablet-width) {
      background-image: none;
      padding: 8px 20px;
    }
  }

  &--current {
    background-color: transparent;
    color: $base-black;
  }
}

.catalog-card__pagination-link span {
  display: none;

  @media (min-width: $tablet-width) {
    display: flex;
  }
}

.catalog-card__pagination-item {
  margin-right: 20px;

  &:last-child {
    margin-right: 0;
  }
}

.catalog-card__sorting-wrapper {
  margin-bottom: 15px;

  @media (min-width: $tablet-width) {
    margin-bottom: 24px;
  }
}
