.map__image {
  width: 320px;
  height: 420px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: 768px;
    height: 480px;
  }

  @media (min-width: $desktop-width) {
    width: 1440px;
    height: 540px;
  }
}

.map__wrapper-picture {
  display: none;

  &--nojs {
    display: block;
  }
}
