.product {
  padding: 20px 20px 60px 20px;
  display: flex;
  flex-direction: column;
  font-family: "Montserrat", "Arial", sans-serif;
  color: $base-black;

  @media (min-width: $tablet-width) {
    padding: 60px 80px 55px 80px;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    padding: 33px 80px 80px 105px;
  }
}

.product__wrapper {
  order: 1;
  display: grid;
  grid-template-columns: 1fr 1fr;

  @media (min-width: $tablet-width) {
    order: 0;
  }

  @media (min-width: $desktop-width) {
    order: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 30px;
    padding-top: 150px;
  }
}

.product__image-wrapper {
  @media (min-width: $tablet-width) {
    margin: 20px auto 0;
  }

  @media (min-width: $desktop-width) {
    margin: 0 0 0 0;
  }
}

.product__title {
  grid-column: 1/-1;
  text-align: center;
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: 48px;
  letter-spacing: 0.72px;
  margin-bottom: 10px;

  @media (min-width: $tablet-width) {
    line-height: 80px;
    font-size: 56px;
    letter-spacing: 1.12px;
    margin-bottom: 16px;
  }
}

.product__badge {
  grid-column: 1/-1;
  order: -1;
  color: $base-primary;
  text-align: center;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.48px;
  text-transform: uppercase;
  margin: 6px 0 4px 0;

  @media (min-width: $tablet-width) {
    margin: 0 0 4px 0;
  }
}

.product__description {
  grid-column: 1/-1;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 32px;
    padding: 0 10px;
  }

  @media (min-width: $desktop-width) {
    text-align: unset;
    padding: 0;
    padding-right: 23px;
    margin-bottom: 40px;
  }
}

.product__price {
  padding: 0 21px 0 24px;

  @media (min-width: $tablet-width) {
    padding: 0 20px 0 0;
  }

  @media (min-width: $desktop-width) {
    padding: 0 20px 0 0;
  }
}

.product__button-buy {
  background-color: $base-primary;
  color: $base-white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  min-width: 113px;
  word-break: break-all;
  padding: 8px 0 8px 0;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: $primary-lighter;
  }

  &:active {
    background-color: $primary-darker;
  }

  &:disabled {
    background-color: $base-gray;
  }
}

.product__old-price {
  color: $base-primary;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  text-decoration-line: line-through;
  padding: 1px 2px 1px 1px;
}

.product__new-price {
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 32px;
}

.product__container {
  grid-column: 1/-1;
  display: flex;
  align-items: center;

  @media (min-width: $tablet-width) {
    margin: auto;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}
