.country {
  margin-bottom: 30px;

  @media (min-width: $tablet-width) {
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 40px;
  }
}

.country__list {
  list-style-type: none;
}

.country__label-checkbox {
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  display: grid;
  grid-template-columns: 42px 1fr;
  cursor: pointer;

  &:hover .country__control::before,
  &:focus .country__control::before {
    border: 2px solid $primary-lighter;
  }

  &:hover .country__control::after,
  &:focus .country__control::after {
    background-clip: padding-box;
    background-color: $base-white;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    grid-column: 1/2;
    grid-row: 1/2;
    margin: 0 2px 0 2px;
  }

  &--disabled .country__control {
    color: $base-gray;
  }

  &--disabled .country__input:checked + .country__control::before {
    background-color: $light-grey;
  }

  &--disabled .country__input:not(:checked) + .country__control::before {
    background-color: $light-grey;
  }

  &--disabled .country__input:checked + .country__control::after {
    background-color: $base-gray;
  }
}

.country__list-item {
  margin-bottom: 20px;
  padding-left: 3px;

  @media (min-width: $tablet-width) {
    padding: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.country__input {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 24px;
  grid-column: 1/2;
  grid-row: 1/2;
  opacity: 0;

  &:checked + .country__control::after {
    margin-left: 20px;
    transition: ease 0.3s;
  }

  &:not(:checked) + .country__control::after {
    margin: 0 2px 0 2px;
    transition: ease 0.3s;
  }

  &:checked + .country__control::before {
    background-color: $base-primary;
    transition: ease 0.3s;
  }
}

.country__control {
  grid-column: 1/-1;
  grid-row: 1/2;
  display: grid;
  grid-template-columns: 42px 1fr;
  gap: 10px;
  align-items: center;

  &::before {
    content: "";
    border-radius: 12px;
    width: 42px;
    background-color: $base-gray;
    height: 24px;
    grid-column: 1/2;
    grid-row: 1/2;
  }

  &::after {
    content: "";
    background-clip: padding-box;
    background-color: $base-white;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    grid-column: 1/2;
    grid-row: 1/2;
    margin: 0 2px 0 2px;
  }
}

.country__legend {
  margin-bottom: 19px;
}
