.price {
  margin-bottom: 31px;

  @media (min-width: $tablet-width) {
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 31px;
  }
}

.price__wrapper-input {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 3px;

  @media (min-width: $tablet-width) {
    justify-content: unset;
    padding: 0;
  }

  &::before {
    content: "";
    width: 8px;
    height: 2px;
    background-color: $base-gray;
    order: 1;
    margin: 1px 18px 0 18px;

    @media (min-width: $tablet-width) {
      margin: 1px 20px 0 20px;
    }
  }
}

.price__price-max {
  order: 2;
}

.price__range-input {
  width: 118px;
  border-radius: 4px;
  background-color: $base-white;
  border: 2px solid $primary-lighter;
  padding: 6px 0 6px;
  text-align: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;

  &:focus {
    border: 2px solid $primary-darker;
  }

  &:disabled {
    border: 2px solid $light-grey;
    background-color: $lightest-grey;
    color: $base-gray;
  }

  @media (min-width: $tablet-width) {
    width: 87px;
  }

  @media (min-width: $desktop-width) {
    width: 134px;
  }
}

.price__range {
  margin-bottom: 35px;
  height: 2px;
  padding-left: 10px;

  @media (min-width: $tablet-width) {
    margin-bottom: 31px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 34px;
  }
}
