.navigation__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;

  @media (min-width: $tablet-width) {
    display: flex;
    align-items: center;
    padding-right: 20px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 40px;
  }

  &--nojs .navigation__additional-nav {
    display: block;
  }

  &--nojs .navigation__list {
    position: static;
  }

  &--nojs .navigation__user-item--nojs {
    display: none;
  }
}

.navigation__logo {
  padding: 4px 0 0 20px;

  @media (min-width: $desktop-width) {
    padding: 0;
    width: 316px;
  }
}

.navigation__logo-description {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
    color: $base-black;
    font-size: 12px;
    font-family: "Montserrat", "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    margin-left: 20px;
  }
}

.navigation__user-list {
  display: flex;
  list-style-type: none;
  justify-content: flex-end;

  @media (min-width: $tablet-width) {
    order: 1;
    margin-left: auto;
  }
}

.navigation__button-login span,
.navigation__button-cart span {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
    color: $base-black;
    font-size: 12px;
    font-family: "Montserrat", "Arial", sans-serif;
    font-style: normal;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: 0.24px;
    text-transform: uppercase;
    margin-left: 8px;
  }
}

.navigation__button-login {
  border: none;
  background-color: transparent;
  padding: 21px 22px 20px 22px;
  cursor: pointer;

  &:active {
    background-color: $base-secondary;
  }

  @media (min-width: $tablet-width) {
    padding: 31px 22px 30px 22px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    align-items: center;
    margin-right: 12px;

    &:hover span {
      color: $base-primary;
    }

    &:active {
      background-color: $base-secondary;
    }

    &:hover .navigation__login-icon,
    &:hover .navigation__cart-icon {
      fill: $base-primary;
    }
  }
}

.navigation__button-cart {
  border: none;
  background-color: transparent;
  padding: 21px 22px 20px 22px;
  cursor: pointer;

  &:active {
    background-color: $base-secondary;
  }

  @media (min-width: $tablet-width) {
    padding: 31px 22px 30px 22px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    align-items: center;

    &:hover span {
      color: $base-primary;
    }

    &:active {
      background-color: $base-secondary;
    }

    &:hover .navigation__login-icon,
    &:hover .navigation__cart-icon {
      fill: $base-primary;
    }
  }
}

.navigation__button {
  position: relative;
  border: none;
  background-color: transparent;
  padding: 30px;
  cursor: pointer;

  &:active {
    background-color: $base-secondary;
  }

  @media (min-width: $tablet-width) {
    display: none;
  }

  &--close::before {
    content: "";
    position: absolute;
    top: 40%;
    left: 50%;
    width: 16px;
    height: 2px;
    background-color: $base-black;
    box-shadow: 0 6px 0 0 $base-black, 0 11px 0 0 $base-black;
    transform: translate(-50%, -40%);
  }

  &--open::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 38%;
    width: 16px;
    height: 1px;
    background-color: $base-black;
    transform: rotate(45deg) translate(0%, -50%);
  }

  &--open::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 40%;
    width: 16px;
    height: 1px;
    background-color: $base-black;
    transform: rotate(-45deg) translate(0%, -50%);
  }
}

.navigation__logo-wrapper {
  display: flex;
}

.navigation__login-icon {
  fill: $base-black;
}

.navigation__cart-icon {
  fill: $base-black;
}
