.slider {
  background-color: $flat-white;

  @media (min-width: $tablet-width) {
    background-color: $base-white;
  }

  @media (min-width: $desktop-width) {
    background-color: $flat-white;
  }

  &--background::before {
    @media (min-width: $tablet-width) {
      content: "";
      background-color: $flat-white;
      position: absolute;
      width: 100vw;
      height: 640px;
    }

    @media (min-width: $desktop-width) {
      content: none;
    }
  }

  &--background.slider--additional-color::before {
    background-color: var(--color);
  }
}

.slider__button-prev,
.slider__button-next {
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 11px 20px;
  border: none;
  height: 656px;
  width: 20px;

  @media (min-width: $tablet-width) {
    height: 640px;
    width: 80px;
  }

  @media (min-width: $desktop-width) {
    height: 717px;
    width: 80px;
  }
}

.slider__button-prev {
  background-image: url("../image/icons/sliderCorner.svg");
  background-repeat: no-repeat;
  background-position: center;
  transform: rotate(180deg);
  margin-left: -10px;

  &:hover,
  &:focus {
    background: linear-gradient(90deg, $base-white 0%, rgba(255, 255, 255, 0) 100%), url("../image/icons/sliderCorner.svg") no-repeat center;
  }

  &:active {
    background: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%), url("../image/icons/sliderCorner.svg") no-repeat center;
  }

  &:disabled {
    background-image: url("../image/icons/sliderCornerDisabled.svg");
  }

  &::after {
    display: none;
  }
}

.slider__button-next {
  background-image: url("../image/icons/sliderCorner.svg");
  background-repeat: no-repeat;
  background-position: center;
  margin-right: -10px;

  &:hover,
  &:focus {
    background: linear-gradient(90deg, $base-white 0%, rgba(255, 255, 255, 0) 100%), url("../image/icons/sliderCorner.svg") no-repeat center;
  }

  &:active {
    background: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%), url("../image/icons/sliderCorner.svg") no-repeat center;
  }

  &::after {
    display: none;
  }

  &:disabled {
    background-image: url("../image/icons/sliderCornerDisabled.svg");
  }
}

.slider__panel-buttons {
  position: absolute;
  top: 22px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  z-index: 1;
}

.slider__wrapper {
  display: flex;
  height: 100%;
  overflow: hidden;
  width: 320px;
  position: relative;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: 768px;
  }

  @media (min-width: $desktop-width) {
    width: 1440px;
    display: grid;
  }
}

.slider__item {
  flex-shrink: 0;
}

.slider__list {
  display: flex;
  width: 100%;
  height: 100%;
  list-style-type: none;
}

.slider__pagination {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
    position: absolute;
    text-align: center;
    margin-bottom: 56px;
  }
}
