.additional-navigation {
  grid-column: 1/-1;

  @media (min-width: $tablet-width) {
    grid-column: 2/3;
    grid-row: 1;
  }

  &--close {
    display: none;

    @media (min-width: $tablet-width) {
      display: block;
      margin-left: 67px;
    }

    @media (min-width: $desktop-width) {
      margin-left: 162px;
    }
  }

  &--open {
    display: block;
  }
}

.additional-navigation__list {
  list-style-type: none;
  text-align: center;
  width: 100%;
  position: absolute;
  z-index: 1;
  background-color: $base-white;

  @media (min-width: $tablet-width) {
    display: flex;
    align-items: center;
    position: static;
  }

  @media (min-width: $desktop-width) {
    width: 459px;
    justify-content: space-between;
  }
}

.additional-navigation__link {
  color: $base-black;
  text-align: center;
  font-size: 12px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: 0.24px;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;

  &:hover,
  &:focus {
    color: $base-primary;
  }

  &:active {
    background-color: $base-secondary;
  }

  @media (min-width: $tablet-width) {
    border: none;
    padding: 33px 20px 32px 20px;
  }

  &--current {
    @media (min-width: $tablet-width) {
      color: $base-primary;
    }
  }

  &--current::before {
    @media (min-width: $tablet-width) {
      position: absolute;
      right: 0;
      bottom: 0;
      left: 0;
      height: 2px;
      background-color: $base-primary;
      content: "";
    }
  }
}

.additional-navigation__item {
  padding: 20px 0 21px 0;
  border-bottom: 1px solid $base-secondary;

  &:active {
    background-color: $base-secondary;
  }

  @media (min-width: $tablet-width) {
    border: none;
    padding: 0;
  }

  &:first-child {
    border-top: 1px solid $base-secondary;

    @media (min-width: $tablet-width) {
      border: none;
    }
  }
}
