.footer__wrapper {
  width: 320px;
  padding: 22px 20px 30px;
  margin: 0 auto;
  font-family: "Montserrat", "Arial", sans-serif;

  @media (min-width: $tablet-width) {
    width: 768px;
    padding: 43px 20px 30px;
  }

  @media (min-width: $desktop-width) {
    width: 1440px;
    display: grid;
    grid-template-columns: 320px 1fr;
    padding: 40px;
  }
}

.footer__logo-text {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    margin: 6px 0 60px 0;
  }
}

.footer__list-social {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px 20px;
}

.footer__heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 39px;

  @media (min-width: $tablet-width) {
    margin-bottom: 26px;
  }

  @media (min-width: $desktop-width) {
    justify-content: unset;
    flex-direction: column;
    align-items: start;
    margin-bottom: 0;
  }
}

.footer__social-link {
  min-width: 40px;
  min-height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover,
  &:focus {
    background-color: $base-secondary;
  }

  &:hover .footer__social-icon,
  &:focus .footer__social-icon {
    fill: $base-primary;
  }

  &:active {
    background-color: $primary-opacity;
  }
}

.footer__shop-list {
  list-style-type: none;
}

.footer__title {
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 20px;
  letter-spacing: 0.28px;
  color: $base-primary;
  margin-bottom: 18px;
}

.footer__link {
  text-decoration: none;
  color: $base-black;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: 0.24px;
  text-transform: uppercase;

  &:hover,
  &:focus {
    color: $primary-lighter;
  }

  &:active {
    color: $primary-darker;
  }
}

.footer__media-list {
  list-style-type: none;
}

.footer__item {
  margin-bottom: 17px;

  &:last-child {
    margin-bottom: 0;
  }
}

.footer__shop {
  position: relative;
  margin-bottom: 47px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    padding-top: 8px;
    padding-left: 27px;
  }

  &::before {
    content: "";
    position: absolute;
    top: -20px;
    left: calc(50% - 50vw);
    width: 100vw;
    height: 1px;
    background-color: $base-secondary;

    @media (min-width: $tablet-width) {
      content: none;
    }
  }
}

.footer__media {
  position: relative;
  margin-bottom: 49px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    padding-top: 8px;
    padding-left: 18px;
  }

  &::before {
    content: "";
    position: absolute;
    top: -20px;
    left: calc(50% - 50vw);
    width: 100vw;
    height: 1px;
    background-color: $base-secondary;

    @media (min-width: $tablet-width) {
      content: none;
    }
  }
}

.footer__contacts {
  position: relative;
  margin-bottom: 41px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    padding-top: 10px;
    padding-left: 10px;
  }

  &::before {
    content: "";
    position: absolute;
    top: -20px;
    left: calc(50% - 50vw);
    width: 100vw;
    height: 1px;
    background-color: $base-secondary;

    @media (min-width: $tablet-width) {
      content: none;
    }
  }
}

.footer__text {
  font-style: normal;
  margin-bottom: 16px;
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 6px;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  padding-top: 2px;

  &::before {
    content: "";
    background-image: url("../image/icons/iconAddress.svg");
    width: 13px;
    height: 16px;
  }
}

.footer__link-phone {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  text-decoration: none;
  color: $base-black;
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 6px;

  &::before {
    content: "";
    background-image: url("../image/icons/iconPhone.svg");
    width: 13px;
    height: 16px;
  }
}

.footer__copyright {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;

  &:hover .footer__copyright-icon {
    fill: $base-primary;
  }

  @media (min-width: $tablet-width) {
    grid-column: 1/-1;
    margin: auto;
  }

  &::before {
    content: "";
    position: absolute;
    top: -20px;
    left: calc(50% - 50vw);
    width: 100vw;
    height: 1px;
    background-color: $base-secondary;

    @media (min-width: $tablet-width) {
      content: none;
    }
  }
}

.footer__copyright-link {
  color: $base-gray;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  margin-right: 4px;
  text-decoration: none;
  font-family: "Montserrat", "Arial", sans-serif;
  margin-bottom: 20px;

  &:hover span {
    color: $base-primary;
    fill: $base-primary;
  }
}

.footer__content-wrapper {
  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    margin-bottom: 38px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 28px;
  }
}

.footer__social-icon {
  fill: $base-gray;
  cursor: pointer;
}

.footer__copyright-icon {
  fill: $base-gray;
}
